import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit, Output, EventEmitter, ViewChild, TemplateRef, Input } from '@angular/core';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { RtfAppService } from '../service/rtf-app.service';
import { RtfApp } from '../entities/rtf-app';
import { i18nFuncHelpInfo } from '../../models/i18n-model/i18n-model';

/**
 * ç³»ç»ç®¡çï¼åè½æä½å·¦æ æãå³åè¡¨å±ç¤º
 */
@Component({
    selector: 'app-rtf-app',
    templateUrl: './rtf-app.component.html',
    styleUrls: ['./rtf-app.component.css']
})
export class RtfAppComponent implements OnInit {

    /**
     * ä¸å¡å¯¹è±¡,è´è´£ä¼ é
     */
    @Input() bizObjectId: string;
    /**
     * éæ©æ¨¡å¼
     */
    @Input()
    gridSelectMode: 'single' | 'multiple';
    /**
     * æ¯å¦åå±å è½½å·¦ä¾§æ ï¼é»è®¤ä¸ºfalse
     */
    isLayeredLoad = false;
    /**
     * ç¡®è®¤äºä»¶
     */
    @Output()
    afterConfirm = new EventEmitter<RtfApp[]>();
    /**
     * å³é­äºä»¶
     */
    @Output()
    afterCancle = new EventEmitter<any>();
    // æ³¨å¥å°å¸®å©çç¡®è®¤ååæ¶æé®
    @ViewChild('btnOkAndCancle')
    btnOkAndCancleRef: TemplateRef<any>;
    @ViewChild('filter')
    filter: any;

    /**
     * åè½æä½å¨éæ°æ®
     */
    data: RtfApp[] = [];
    /**
     * å½åUIæ¾ç¤ºå¨è½æä½åè¡¨æ°æ®
     */
    gridData: RtfApp[];

    /**
     * å·¦ä¾§æ åè¡¨æ°æ®
     */
    treeData: RtfApp[];
    /**
     * ç¼å­çèååç»ææ°æ®
     */
    allAppList: any[] = [];
    /**
     * å½åéä¸­æ°æ®
     */
    curSelectedKeys: string[] = [];
    selectableSettings: SelectableSettings;
    selectedTreeNode: any;
    title = 'åè½èåå¸®å©';
    private currentLangCode: string;
    constructor(
        private rtfAppService: RtfAppService
    ) {
    }

    ngOnInit() {
        this.selectableSettings = {
            checkboxOnly: true,
            // é»è®¤åé
            mode: !this.gridSelectMode ? 'single' : this.gridSelectMode,
            enabled: true
        };
        if (this.isLayeredLoad === false) {
            const allFuncsStream = this.rtfAppService.getAppList();
            this.loadTreeData(allFuncsStream);
        } else {
            this.loadTreeDataByGrandParentId('0');
        }
    }
    onKeyUp(arg: any) {
        if (arg && arg.keyCode === 13) {
            this.searchData(this.filter.nativeElement.value);
        }
    }
    /**
     * æç´¢
     * @param filterValue æ¥è¯¢æ¡ä»¶
     */
    searchData(filterValue: string) {
        // ç©ºçæç´¢æ¯å¨é¨æ°æ®
        if (filterValue === null || filterValue === '') {
            this.gridData = this.data.concat();
            return;
        }
        const filter = filterValue.toLowerCase();
        // æç´¢å°ä¸çº§æ¶
        this.gridData = this.data.filter(item =>
            item.Code.toLowerCase().includes(filter) ||
            item.Name.toLowerCase().includes(filter));
    }

    /**
     * ä¸å¡å¯¹è±¡ååå
     * @param arg ä¸å¡å¯¹è±¡åæ°
     */
    leftAppTreeChanged(arg: RtfApp) {
        if (!this.data || this.data.length === 0) {
            return;
        }
        const selectedId = arg.Id;
        const selectedPath = arg.Path;
        // æ ¹æ®ç¶å­å³ç³»è¿æ»¤å½åçé¢æ°æ®ï¼åå«ä¸ä¸çº§
        this.gridData = this.data.filter(item =>
            item.ParentId === selectedId ||
            item.Path.startsWith(selectedPath)
        );
    }
    onAppTreeExpanded(arg: any) {
        if (this.isLayeredLoad === false) {
            return;
        }
        if (this.isContainGrandChildNode(arg) === false) {
            this.loadTreeDataByGrandParentId(arg.Id);
        }
    }

    /**
     * è·åéä¸­æ°æ®
     */
    getSelectedObject(): RtfApp[] {
        const result = this.data
            .filter(item =>
                this.curSelectedKeys.findIndex(select => select === item.Id) >= 0
            );
        return result;
    }

    /**
     * ç¡®è®¤
     */
    confirm() {
        const result = this.getSelectedObject();
        // this.selectedTreeNode.selectedInvok = result;
        this.afterConfirm.next(result);
        // åæ¶-ç®çæ¯å¨è¿éè®©æ¨¡ææ¡æ¶å¤±
        this.cancle();
    }
    /**
     * åæ¶
     */
    cancle() {
        this.afterCancle.next();
    }

    public setCurrentLangCode(langCode: string) {
        this.currentLangCode = langCode;
        this.title = this.getResOnCurrentLangeContext('TitleName');
    }
    public getResOnCurrentLangeContext(resKey: string): string {
        const resValue = i18nFuncHelpInfo[this.currentLangCode][resKey];
        return resValue;
    }
    private isContainGrandChildNode(node: any): boolean {
        if (!('1' === node.Layer || '2' === node.Layer)) {
            return true;
        }
        const parentId = node.Id;
        const sonList = this.allAppList.filter(
            f => f.FParentid === parentId
        );
        let res = false;
        for (let i = 0; i < sonList.length; i++) {
            const grandList = this.allAppList.filter(o => o.FParentid === sonList[i].FID);
            if (grandList && grandList.length > 0) {
                res = true;
                break;
            }
        }
        return res;
    }
    private loadTreeDataByGrandParentId(grandParentId: string) {
        // åå±å è½½
        const stream = this.rtfAppService.getDataByGrandParentId(grandParentId);
        this.loadTreeData(stream);
    }

    private loadTreeData(stream: any) {
        stream.subscribe(
            (res) => {
                if (res) {
                    res.forEach(element => {
                        const temp = {
                            Id: element.id,
                            Code: element.code,
                            Name: element.name,
                            ParentId: element.parentId,
                            Layer: element.layer,
                            Path: element.path,
                            SortOrder: element.sortOrder,
                            Child: element.child,
                            InvokingConfig: element.invokingConfig
                        };
                        this.allAppList.push(temp);
                    });
                }
                // å·¦ä¾§æ 
                this.treeData = this.allAppList.filter(item => item.Layer <= 3);
                // èåæç»æ°æ®
                this.data = this.allAppList.filter(item => item.Layer >= 4);
            },
            (err: HttpErrorResponse) => {
                console.log('è·åèåæ°æ®å¤±è´¥');
            }
        );
    }
}

